#container {

    a {
        text-decoration: none;

        h1 {
            position: relative;
            top: 20%; left: 50%;
            transform: translate(-50%,-20%);
            color: $black;
            font-family: $font-family-title;
            font-size: $font-size-h1;
            text-decoration: none;
            text-align: center;
        }
    }


    #visual {
        box-sizing: border-box;
        float: left;
        background-image: url('../img/visual_league.svg');
        background-size: cover;
        background-repeat: no-repeat;
        background-position: bottom; 
        background-color: $black;
        height: 100vh;
        width:50%;
        border-right: 1.5px solid $green;


        h1 {
            color: $green;
        }

        img {
            position: absolute;
            top: 20%; left: 47.5%;
            transform: translate(-47.5%,-20%);
            width: 5%;
        }
    
    }
    
    #web {
        box-sizing: border-box;
        float: left;
        background-image: url('../img/websquad.svg');
        background-size: contain;
        background-repeat: no-repeat;
        background-position: bottom; 
        background-color: $black;
        height: 100vh;
        width:50%;
        border-left: 1.5px solid $green;
        transition: transform 0.3s;

        h1 {
            color: $green;
        }

        img {
            position: absolute;
            top: 20%; right: 43%;
            transform: translate(-43%,-20%);
            width: 5%;
        }

        &.open {
            transform: translateX(45vw);
        }

    }


}

